import React from 'react';
import CurrentUserContext from '../contexts/CurrentUserContext'

function Card ({cardId, src, nameImg, like, onCardClick, onCardLike, ownerId, setConfirmOpen, setConfirmAction, setConfirmObj}) {

  const currentUser = React.useContext(CurrentUserContext);

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, —è–≤–ª—è–µ–º—Å—è –ª–∏ –º—ã –≤–ª–∞–¥–µ–ª—å—Ü–µ–º —Ç–µ–∫—É—â–µ–π –∫–∞—Ä—Ç–æ—á–∫–∏
  const isOwn = ownerId === currentUser._id;
  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ —É–¥–∞–ª–µ–Ω–∏—è
  const cardDeleteButtonClassName = (
    `element__trash ${isOwn ? ' ' : 'element__trash_invisible'}`
  ); 

  // –û–ø—Ä–µ–¥–µ–ª—è–µ–º, –µ—Å—Ç—å –ª–∏ —É –∫–∞—Ä—Ç–æ—á–∫–∏ –ª–∞–π–∫, –ø–æ—Å—Ç–∞–≤–ª–µ–Ω–Ω—ã–π —Ç–µ–∫—É—â–∏–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–º
  const isLiked = like.some(i => i._id === currentUser._id);

  // –°–æ–∑–¥–∞—ë–º –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é, –∫–æ—Ç–æ—Ä—É—é –ø–æ—Å–ª–µ –∑–∞–¥–∞–¥–∏–º –≤ `className` –¥–ª—è –∫–Ω–æ–ø–∫–∏ –ª–∞–π–∫–∞
  const cardLikeButtonClassName = (
  `element__like ${isLiked ? 'element__like_type_active' : ' '}`); 

  function handleImgClick(){
    onCardClick({src, nameImg});
  }

  function handleLikeClick() {
    // console.log("========LIKE=======");
    // console.log(like);
    onCardLike(like, cardId);
  }

  function handleCardDelete(){
    // console.log("handleCardDelete");
    setConfirmOpen();
    setConfirmAction('cardDelete');
    setConfirmObj({
      ownerId: ownerId,
      cardId: cardId
    })
  }

  return (
    <div className="element">
      <img src={src} alt={nameImg} className="element__img" onClick={handleImgClick}/>
      <div className="element__name">
        <h3 className="element__title">{nameImg}</h3>
        <div className="element__likes">
          <button className={cardLikeButtonClassName} type="button" onClick={handleLikeClick}></button>
          <p className="element__like-counter">{like.length}</p>
        </div>
      </div>
      <button className={cardDeleteButtonClassName} type="button" onClick={handleCardDelete}></button>
    </div>
  );
}

export default Card;